import { createStore, compose, applyMiddleware, combineReducers } from 'redux';
import thunk from 'redux-thunk';
import { cartReducer } from './reducers/cartReducers';
import { getWithExpiry } from './utils';
import {
  orderCreateReducer,
  orderDeleteReducer,
  orderDeliverReducer,
  orderDetailsReducer,
  orderListReducer,
  orderMineListReducer,
  orderPaymentMTNReducer,
  orderPaymentAirtelReducer,
  orderPayReducer,
} from './reducers/orderReducers';
import {
  productCategoryListReducer,
  productCreateReducer,
  productDeleteReducer,
  productDetailsReducer,
  productListPopularReducer,
  productListReducer,
  productListTopRatedReducer,
  productReviewCreateReducer,
  productUpdateReducer,
} from './reducers/productReducers';
import {
  userAddressMapReducer,
  userDeleteReducer,
  userDetailsReducer,
  userListReducer,
  userRegisterReducer,
  userSigninReducer,
  userTopSellerListReducer,
  userUpdateProfileReducer,
  userUpdateReducer,
} from './reducers/userReducers';
import { projectCreateReducer, projectDeleteReducer, projectDetailsReducer, projectListCompletedReducer, projectListOngoingReducer, projectListReducer, projectUpdateReducer } from './reducers/projectReducers';
import { serviceCreateReducer, serviceDeleteReducer, serviceDetailsReducer, serviceListReducer, serviceUpdateReducer } from './reducers/serviceReducers';

const initialState = {
  userSignin: {
    userInfo: localStorage.getItem('userInfo')
      ? JSON.parse(localStorage.getItem('userInfo'))
      : null,
  },
  cart: {
    cartItems: localStorage.getItem('cartItems')
      ? JSON.parse(localStorage.getItem('cartItems'))
      : [],
    shippingAddress: localStorage.getItem('shippingAddress')
      ? JSON.parse(localStorage.getItem('shippingAddress'))
      : {},
    paymentMethod: 'Airtel',
  },
  orderPayAirtel: {
    paymentInfoAirtel:  getWithExpiry('paymentInfoAirtel')
    ? JSON.parse(getWithExpiry('paymentInfoAirtel'))
    : null,
  },
  orderPayMTN: {
    paymentInfoMTN:  getWithExpiry('paymentInfoMTN')
    ? JSON.parse(getWithExpiry('paymentInfoMTN'))
    : null,
  }
};
const reducer = combineReducers({
  serviceList: serviceListReducer,
  serviceCreate: serviceCreateReducer,
  serviceUpdate: serviceUpdateReducer,
  serviceDelete: serviceDeleteReducer,
  serviceDetails: serviceDetailsReducer,
  projectList: projectListReducer,
  projectListCompleted: projectListCompletedReducer,
  projectListOngoing: projectListOngoingReducer,
  projectDetails: projectDetailsReducer,
  projectCreate: projectCreateReducer,
  projectUpdate: projectUpdateReducer,
  projectDelete: projectDeleteReducer,
  productList: productListReducer,
  productListPopular: productListPopularReducer,
  productListTopRated: productListTopRatedReducer,
  productDetails: productDetailsReducer,
  cart: cartReducer,
  userSignin: userSigninReducer,
  userRegister: userRegisterReducer,
  orderCreate: orderCreateReducer,
  orderDetails: orderDetailsReducer,
  orderPayMTN: orderPaymentMTNReducer,
  orderPayAirtel: orderPaymentAirtelReducer,
  orderPay: orderPayReducer,
  orderMineList: orderMineListReducer,
  userDetails: userDetailsReducer,
  userUpdateProfile: userUpdateProfileReducer,
  userUpdate: userUpdateReducer,
  productCreate: productCreateReducer,
  productUpdate: productUpdateReducer,
  productDelete: productDeleteReducer,
  orderList: orderListReducer,
  orderDelete: orderDeleteReducer,
  orderDeliver: orderDeliverReducer,
  userList: userListReducer,
  userDelete: userDeleteReducer,
  userTopSellersList: userTopSellerListReducer,
  productCategoryList: productCategoryListReducer,
  productReviewCreate: productReviewCreateReducer,
  userAddressMap: userAddressMapReducer,
});
const composeEnhancer = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = createStore(
  reducer,
  initialState,
  composeEnhancer(applyMiddleware(thunk))
);

export default store;
